import React, { Component } from 'react'
import DayPicker from 'react-day-picker';
import 'react-day-picker/lib/style.css';


class CustomDayPicker extends Component {
constructor(props){
    super(props);
    
    this.WEEKDAYS_LONG = {
        pl: [
    'Poniedziaek',
    'Wtorek',
    'roda',
    'Czwartek',
    'Pitek',
    'Sobota',
    'Niedziela',
  ]
    };
    this.WEEKDAYS_SHORT = {
        pl: ['Nd', 'Pn', 'Wt', 'r', 'Cz', 'Pi', 'Sb']
    };
    this.MONTHS = {
        pl: [
    'Stycze',
    'Luty',
    'Marzec',
    'Kwiecie',
    'Maj',
    'Czerwiec',
    'Lipiec',
    'Sierpie',
    'Wrzesie',
    'Padziernik',
    'Listopad',
    'Grudzie',
  ]
    };
    this.FIRST_DAY_OF_WEEK = {
        pl: 1
    };
    this.LABELS = {
        pl: {
            nextMonth: 'Nastpny miesic',
            previousMonth: 'Poprzedni miesic'
        }
    };
}
  render() {
    return (
       <DayPicker
         
          months={ this.MONTHS.pl}
          weekdaysLong={ this.WEEKDAYS_LONG.pl}
          weekdaysShort={ this.WEEKDAYS_SHORT.pl}
          firstDayOfWeek={ this.FIRST_DAY_OF_WEEK.pl}
          labels={ this.LABELS.pl} />
    );
  }
}


export default CustomDayPicker;



